@import url('https://fonts.googleapis.com/css?family=Poiret+One');
@import url('https://fonts.googleapis.com/css?family=Raleway');

.projects-nav {
  display: -ms-flexbox;
  display: flex;
  /* border: 2px solid pink; */
  height: 2em;
  width: 100vw;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-line-pack: center;
      align-content: center;
  /* color: white; */
  /* padding-top: 0.8em; */
  font-size: 3em;
  /* position: absolute;
  top: 0; */
}

.project-link {
  height: 2em;
  font-family: 'Raleway', sans-serif;
  color: rgb(255, 248, 240);
  font-size: 0.6em;
  padding: 1px;
  width: 5em;
  border: 2px solid rgb(252, 196, 93);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  text-align: center;
  vertical-align: center;
  padding-top: 0.2em;
  transition: 0.3s;
}

.project-link:hover {
  border: 2px solid rgb(252, 196, 93);
  background-color: rgb(252, 196, 93);
}


.project-container {
  display: -ms-flexbox;
  display: flex;
  /* border: 5px solid black; */
  -ms-flex-pack: center;
      justify-content: center;
  background-color: rgb(17, 46, 60);
  width: 100%;
  height: 90vh;
}


