.arp-container {
/* border: 2px solid red; */
height: 100%;
background-color: rgb(255, 248, 240)
}

.pattern-display {

}

.keyboard-container {
  height: 7em;
  padding: 2em;
  padding-top: 0;
  padding-bottom: 2em;
}

.note-div {
  height: 9vh;
  border-radius: 3px;
  -webkit-box-shadow: 0px 4px 5px black;
          box-shadow: 0px 4px 5px black;
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
}

.note-div:active {
  height: 9.5vh;
  -webkit-box-shadow: 0px 0px 1px black;
          box-shadow: 0px 0px 1px black;
}

.button-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  padding-bottom: 2em;
}

.buttons {
  background: none;
  border: 2px solid black;
  font-size: 1.5em;
  border-radius: 3px;
}

.freq-container {

}

.parameter-text {

}

.pattern-container {

}

.wave-container {

}

.menu-select {

}

.type-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  padding: 1em 0 2em 0;
}

.slider-container {
  padding-left: 2em;
  padding-right: 2em;
}
.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;   
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  -o-transition: opacity .2s;
  transition: opacity .2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 1.3em;
  height: 1.3em;
  border-radius: 50%; 
  background: rgb(0, 123, 204);
  cursor: pointer;
}

.slider::after {
  background: black;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #4CAF50;
  cursor: pointer;
}



