@import url("https://fonts.googleapis.com/css?family=Orbitron");

.clockContainer {
  background-color: none;
  padding-top: 3rem;
}

.digi-clock {
  font-family: 'Orbitron', sans-serif;
  font-size: 5em;
  text-align: center;
  color: rgba(0, 0, 0, 0);
  -webkit-animation-name: blur;
          animation-name: blur;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  margin: 0.5em 0 1.5em 0;
}

@-webkit-keyframes blur {
  from { text-shadow: 10px 2px 5px black;
  }
  to { text-shadow: 10px 2px 8px black;
  }
}

@keyframes blur {
  from { text-shadow: 10px 2px 5px black;
  }
  to { text-shadow: 10px 2px 8px black;
  }
}

.clock-container {
  width: 30rem;
  height: 30rem;
  border-radius:50%;
  margin:50px auto;
  position: relative;
  padding:2rem;
  -webkit-animation-name: blur-box;
          animation-name: blur-box;
  -webkit-animation-duration: 15s;
          animation-duration: 15s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.clock-face {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px); 
}

.hand {
  width: 50%;
  height: 6px;
  position: absolute;
  top: 50%;
  -webkit-transform-origin: 100%;
      -ms-transform-origin: 100%;
          transform-origin: 100%;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transition: all 0.02;
  -o-transition: all 0.02;
  transition: all 0.02;
  -webkit-transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
       -o-transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
          transition-timing-function: cubic-bezier(0, 2.32, 0.2, 1.35);
  -webkit-animation-name: blur-text;
          animation-name: blur-text;
  -webkit-animation-duration: 15s;
          animation-duration: 15s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

@-webkit-keyframes blur-box {
  from { -webkit-box-shadow: 0em 0em -5em black; box-shadow: 0em 0em -5em black;
  }
  to { -webkit-box-shadow: 0em 0em 5em 5em white; box-shadow: 0em 0em 5em 5em white;
  }
}

@keyframes blur-box {
  from { -webkit-box-shadow: 0em 0em -5em black; box-shadow: 0em 0em -5em black;
  }
  to { -webkit-box-shadow: 0em 0em 5em 5em white; box-shadow: 0em 0em 5em 5em white;
  }
}

@-webkit-keyframes blur-text {
  from { -webkit-box-shadow: inset 0 0 0.1em rgba(255, 255, 255, 0); box-shadow: inset 0 0 0.1em rgba(255, 255, 255, 0);
  }
  to { -webkit-box-shadow: inset 0 0 1em 0.1em white; box-shadow: inset 0 0 1em 0.1em white;
  }
}

@keyframes blur-text {
  from { -webkit-box-shadow: inset 0 0 0.1em rgba(255, 255, 255, 0); box-shadow: inset 0 0 0.1em rgba(255, 255, 255, 0);
  }
  to { -webkit-box-shadow: inset 0 0 1em 0.1em white; box-shadow: inset 0 0 1em 0.1em white;
  }
}



